<template>
  <div class="container mt-4">
    <Thankyou
      :properties="properties"
      @refreshList="getPropertiesByLocation"
      class="is-mobile-device"
    />
    <ThankyouDesktop
      :properties="properties"
      @refreshList="getPropertiesByLocation"
      class="is-desktop-device"
    />
  </div>
</template>

<script>
import Thankyou from '@/components/postingRequestForm/Thankyou'
import ThankyouDesktop from '@/components/desktop/postingRequestForm/ThankyouDesktop'

import { fetchPropertiesByLocation } from '@/api/properties'
export default {
  components: {
    Thankyou,
    ThankyouDesktop,
  },
  data() {
    return {
      properties: [],
    }
  },
  created() {
    this.getPropertiesByLocation()
  },
  methods: {
    async getPropertiesByLocation() {
      let locationId = this.$route.query?.locationId
      let request_id = this.$route.query?.request_id
      try {
        const response = await fetchPropertiesByLocation(locationId, request_id)
        this.properties = response.Data.properties
      } catch (error) {
        console.log(error, 'error')
      }
    },
  },
}
</script>
<style lang="scss" scoped>
.container {
  background-color: $mainColorDark;
  margin-top: -40px !important;
  padding-top: 58px;
  margin: 0;
  min-width: 100%;
  min-height: 100vh;
}
h1 {
  font-size: 42px;
}
h3 {
  font-size: 18px;
}
p {
  color: rgba(255, 255, 255, 0.6);
}

svg {
  height: 64px;
  width: 64px;
}
</style>
