<template>
  <div class="d-flex">
    <div>
      <AppImage :src="property.image" :quality="50" class="property-image" />
    </div>
    <div :class="isRTL ? 'mr-4' : 'ml-4'">
      <div class="d-flex justify-content-between align-items-start">
        <h1 class="font-size-normal text-white font-weight-bold">
          {{ property.property_name }}
        </h1>
        <b-button
          style="background-color: transparent"
          variant="link"
          class="side-btn favourite-button p-0"
          @click="
            $emit('updateFavorite', {
              propertyId: property.property_id,
              term: property.term,
            })
          "
        >
          <svg-icon
            :name="
              property.isFav ? 'grey-checked-heart' : 'grey-unchecked-heart'
            "
            fill="#204ECF"
            width="24px"
            height="24px"
          />
        </b-button>
      </div>
      <div class="property-meta d-flex justify-content-between">
        <div class="meta-item">
          <svg-icon name="grey-bed" fill="#FFFFFF" height="14" width="14" />
          <span class="font-size-smaller text-white"
            >{{ property.no_of_bedroom }}Bed</span
          >
        </div>
        <div class="meta-item" :class="isRTL ? 'mr-2' : 'ml-2'">
          <svg-icon name="grey-bath" fill="#FFFFFF" height="14" width="14" />
          <span class="font-size-smaller text-white"
            >{{ property.no_bathrooms }}Bath</span
          >
        </div>
        <div class="meta-item" :class="isRTL ? 'mr-2' : 'ml-2'">
          <svg-icon name="grey-area" fill="#FFFFFF" height="14" width="14" />
          <span class="font-size-smaller text-white"
            >{{ property.property_area }}M</span
          >
          <span class="text-white">&#178;</span>
        </div>
      </div>
      <div class="mt-2">
        <span class="font-size-normal text-white font-weight-bold"
          >{{ priceRange }}
        </span>
        <span class="font-size-smaller price-duration">{{
          priceDuration
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('CoreModule')
export default {
  props: {
    property: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    ...mapGetters(['isRTL']),

    priceRange() {
      return this.property.term == 1
        ? `$ ${this.property.min_monthly_price} ${this.$t('common.egp')} - ${
            this.property.max_monthly_price
          } ${this.$t('common.egp')}`
        : ` ${this.property.min_daily_price} ${this.$t('common.egp')} - ${
            this.property.max_daily_price
          } ${this.$t('common.egp')}`
    },

    priceDuration() {
      return this.property.term == 1
        ? '/ ' + this.$t('common.monthly')
        : '/' + this.$t('common.daily')
    },
  },
}
</script>

<style scoped lang="scss">
.property-image {
  width: 86px;
  height: 86px;
}

.property-meta {
  .meta-item {
    background: rgba(255, 255, 255, 0.16);
    border-radius: 8px;
    padding: 8px 12px;
    min-width: 84px;
  }
}
.price-duration {
  color: rgba(255, 255, 255, 0.64);
}
</style>
