<template>
  <b-row class="pb-3">
    <b-col cols="12">
      <b-button class="bg-transparent border-0 p-0" @click="closeClick">
        <svg-icon name="close-white" height="30" width="30" />
      </b-button>
    </b-col>

    <b-col cols="12">
      <svg-icon name="thank-you" height="64" width="64" />
    </b-col>
    <b-col cols="12" class="mt-2">
      <h3 class="text-white font-size-semi-large thanks-subtitle">
        {{ $t('property.thank_you_subtitle') }}
      </h3>
    </b-col>
    <b-col cols="12">
      <hr />
    </b-col>
    <b-col cols="12" class="mt-2">
      <h1 class="text-white font-size-semi-large">
        {{ $t('property.similar_properties_title') }}
        <nuxt-link
          class="city-name"
          :to="localePath(`/properties?keyword=${cityName}`)"
          >{{ cityName }}</nuxt-link
        >
      </h1>
    </b-col>
    <b-col
      cols="12"
      v-for="property in properties"
      :key="property.id"
      class="mt-4"
    >
      <SimilarPropertyCard
        :property="property"
        @updateFavorite="updateFavorite"
      />
    </b-col>
    <b-col cols="12" class="mt-4">
      <nuxt-link class="view-all" :to="localePath('/search')">
        {{ $t('common.explore_more') }}</nuxt-link
      >
    </b-col>
  </b-row>
</template>

<script>
import SimilarPropertyCard from '@/components/shared/SimilarPropertyCard'
import { updateFavoriteProperties } from '@/api/properties'

export default {
  name: 'thank-you',
  components: {
    SimilarPropertyCard,
  },
  props: {
    properties: {
      type: Array,
      default: [],
    },
  },
  computed: {
    cityName() {
      return this.$route.query?.city
    },
  },

  methods: {
    async updateFavorite(data) {
      let payload = { prop_id: data.propertyId, term: data.term }
      try {
        const response = await updateFavoriteProperties(payload)
        if (response.Message === 'success') {
          this.$emit('refreshList')
        }
      } catch (error) {}
    },
    closeClick() {
      this.$router.push(this.localePath('/search'))
    },
  },
}
</script>
<style lang="scss" scoped>
.thankyou-title {
  font-size: 42px;
}

p {
  color: rgba(255, 255, 255, 0.6);
}

hr {
  background: #fff;
}
.view-all {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #204ecf;
  border-radius: 8px;
  padding: 12px 24px;
  border-radius: 8px;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}

.city-name {
  text-decoration: underline !important;
}

.thanks-subtitle {
  line-height: 1.4;
}
</style>
