<template>
  <b-container>
    <b-row class="pb-3">
      <b-col cols="12" class="d-flex flex-column align-items-center">
        <svg-icon name="thank-you" />
        <h1 class="text-white thankyou-title">
          {{ $t('property.thank_you') }}
        </h1>
        <h3 class="text-white font-size-semi-large">
          {{ $t('property.thank_you_subtitle') }}
        </h3>
      </b-col>
    </b-row>
    <b-row class="pb-3">
      <b-col cols="12" class="mt-2">
        <h1 class="text-white font-size-semi-large text-center">
          {{ $t('property.similar_properties_title') }}
          <nuxt-link
            class="city-name"
            :to="localePath(`/properties?keyword=${cityName}`)"
            >{{ cityName }}</nuxt-link
          >
        </h1>
      </b-col>
    </b-row>
    <b-row class="justify-content-center">
      <b-col
        cols="3"
        md="4"
        v-for="property in properties"
        :key="property.id"
        class="mt-4"
      >
        <PropertyCard :cardData="property" :isThankYouPage="true" />
      </b-col>
    </b-row>
    <b-row class="pb-4">
      <b-col cols="12" class="mt-4 d-flex justify-content-center">
        <nuxt-link class="view-all" :to="localePath('/search')">
          {{ $t('common.explore_more') }}</nuxt-link
        >
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import SimilarPropertyCard from '@/components/shared/SimilarPropertyCard'
import { updateFavoriteProperties } from '@/api/properties'
import PropertyCard from '@/components/shared/PropertyCard'

export default {
  name: 'thank-you',
  components: {
    SimilarPropertyCard,
    PropertyCard,
  },
  props: {
    properties: {
      type: Array,
      default: [],
    },
  },
  computed: {
    cityName() {
      return this.$route.query?.city
    },
  },
  methods: {
    async updateFavorite(data) {
      let payload = { prop_id: data.propertyId, term: data.term }
      try {
        const response = await updateFavoriteProperties(payload)
        if (response.Message === 'success') {
          this.$emit('refreshList')
        }
      } catch (error) {}
    },
  },
}
</script>
<style lang="scss" scoped>
.thankyou-title {
  font-size: 42px;
}

p {
  color: rgba(255, 255, 255, 0.6);
}

svg {
  height: 64px;
  width: 64px;
}

hr {
  background: #fff;
}
.view-all {
  background: #0877f1;
  border-radius: 8px;
  padding: 12px 24px;
  border-radius: 8px;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}

.city-name {
  text-decoration: underline !important;
}
</style>
